import event from 'events';

class Communicate extends event.EventEmitter {
    constructor() {
        super();
        this.originsWhiteList = ["https://www.uuabc.com", "https://local.uuabc.com"];
        this.outer = '';
        this.origin = '';
        this.init();
    }

    init() {
        window.addEventListener("message", this.receiveMessage.bind(this), false);
    }

    receiveMessage(event) {
        // 我们能信任信息来源吗？
        // if (!this.originsWhiteList.includes(event.origin))
        //     return;
        // event.source 就是来源页面
        // 假设你已经验证了所受到信息的origin (任何时候你都应该这样做), 一个很方便的方式就是把enent.source
        // 作为回信的对象，并且把event.origin作为targetOrigin
        this.outer = event.source;
        this.origin = event.origin;
        const {
            type,
            data
        } = event.data;
        if (!type) return;
        switch (type) {
            case 'config':
                this.emit('config', data);
                break;
        }
    }

    /**
     * @param {String} type event type 
     * @param {Object} data 
     */
    postMessage(type, data = {}) {
        if (!window.parent) return;
        window.parent.postMessage({
            type,
            data
        }, '*');
    }
    /**
     * @param {Object} data 
     */
    report(data) {
        this.postMessage('report', data);
    }

    goBack() {
        this.postMessage('goBack');
    }

    goAhead() {
        this.postMessage('goAhead');
    }
}

export {
    Communicate
}